import styles from './stretchedButton.module.css'
import FeatherIcon from '../../feather/featherIcon'

export default function StretchedButton({title, icon, onClick}) {
    return (
        <div className={styles.stretchedButton} onClick={ () => { if(onClick) onClick(); } } >
            <span>
                <FeatherIcon name={icon} />
            </span>
            <span class={styles.title}>{title}</span>
        </div>
    );
}