import './gallery.module.css';
import React from 'react';
import GalleryRow from './galleryRow'
import cn from "classnames";

export default function Gallery({className, rowNum, colNum, images, selected, onClick}){
    let selectedItem = 0
    return <div className={cn({
        [className]: !!className
    })}>
         {[...Array(rowNum).keys()].map(r => {
            selectedItem = (((r * colNum <= selected) && (selected < (r + 1) * colNum) ) ? selected % colNum : -1);
            return(<GalleryRow 
                images={images.slice(r * colNum, r * colNum + colNum)} 
                selected={selectedItem}
                onClick={onClick}
            />)
         })}
    </div>
}