import SwitchButton from '../../elements/switchButton/swtichButton'
import ToggleButton from '../../elements/toggleButton/toggleButton'
import FeatherIcon from '../../feather/featherIcon'
import styles from './Edit.module.css';
import { SliderPicker } from 'react-color';
import {useState} from "react";
import Gallery from '../../gallery/gallery'
import StretchedButton from '../../elements/stretchedButton/stretchedButton'
import { useHistory } from 'react-router';
import cn from "classnames";

function obtainColor(color){
    let c = color.slice(1, color.length);
    return {r: parseInt(c.slice(0,2), 16),
            g: parseInt(c.slice(2,4), 16),
            b: parseInt(c.slice(4,6), 16)}
}

window.allocator = function (size){
    return new Uint8Array(size);
}

let Go;

export default function Edit() {

    const history = useHistory();

   if(!Go){
        // Script is imported in index.html placed in public folder
    const {Go:MGo} = window
    Go = MGo;
    if (!WebAssembly.instantiateStreaming) { // polyfill
        WebAssembly.instantiateStreaming = async (resp, importObject) => {
            const source = await (await resp).arrayBuffer();
            return await WebAssembly.instantiate(source, importObject);
        };
    }

    const go = new Go();
    let inst;
    WebAssembly.instantiateStreaming(fetch("./main.wasm"), go.importObject).then(async (result) => {
        // mod = result.module;
        inst = result.instance;
        await go.run(inst);
    }).catch((err) => {
        console.error(err);
    });

   }

    const [state, setState] = useState({
        currentStyleIndex:1,
        currentColorIndex:0,
        sColor: "#ffffff",
        darkLayer:false,
        lightLayer:false,
        previousSetting:"Empty",
        selectedMode: 0,
        inferenceStatus: "Done"
    });
    

    let options = ""
    const modeToColor = {
        'mode1': '#ff0000', 
        'mode2': '#ffffff', 
        'mode3': '#90ee90', 
        'mode4': '#ffff00',
        'mode5': "#8b0000",
        'mode6': '#006400',
    }
    
    const p = String(state.currentColorIndex) + String(state.sColor) + String(state.darkLayer) + String(state.lightLayer)

    let applyButton = (
        <div class={styles.buttonHolder}>
            <StretchedButton 
                title="اعمال تغییرات"
                icon="check"
                id="hello"
                onClick={ () => {
                    let {r,g,b} = obtainColor(state.sColor);
                    setState( {...state, 
                        previousSetting: p,
                        inferenceStatus: "Doing",
                        
                    });
                    let storeGet = c => window.localStorage.getItem(c);
                    window.colorize(storeGet("image"), storeGet("mask"), r, g, b, state.lightLayer, state.darkLayer, state.currentColorIndex + 1, (x) => {
                        document.getElementById('yourPhoto').src = URL.createObjectURL(
                            new Blob([x.buffer], { type: 'image/jpeg' })
                        );

                        setState( {...state, inferenceStatus: "Done" } )
                    });
                } }
            />       
        </div>
    );
    
    // if Setting is same or we're in not in pro mode do not show apply button
    if(p === state.previousSetting || state.currentStyleIndex === 1) {
        applyButton = ""
    }
    
    // we are Not in pro mode
    if (state.currentStyleIndex === 1) {
        options =
            <Gallery
                className={styles.gallery}
                rowNum={2}
                colNum={3} 
                images={["mode1.png", "mode2.png", "mode3.png", "mode4.png", "mode5.png", "mode6.png"]}
                selected={state.selectedMode - 1}
                onClick = {(mode) => {
                    mode = mode.split('.')[0]
                    setState({...state, 
                        sColor: modeToColor[mode], 
                        selectedMode: parseInt(mode[mode.length - 1]),
                        inferenceStatus: "Doing"
                    
                    })
                    let {r,g,b} = obtainColor(modeToColor[mode]);
                    let storeGet = c => window.localStorage.getItem(c);
                    window.colorizeAuto(storeGet("image"), storeGet("mask"), r, g, b, (x) => {
                        document.getElementById('yourPhoto').src = URL.createObjectURL(
                            new Blob([x.buffer], { type: 'image/jpeg' })
                        );

                        setState( {...state, 
                            inferenceStatus: "Done",
                            selectedMode: parseInt(mode[mode.length - 1]),
                            sColor: modeToColor[mode], 
                        } )
                    });
                }}

            />
    } else {
        options = (
            <div class={styles.Options}>
                <div>
                    <div>رنگ</div>
                    <div className={styles.colorPickerContainer}>
                        <SliderPicker
                        className={styles.colorPicker}
                        color={state.sColor}
                        onChange={(color) => setState({...state, sColor: color.hex})}
                        />
                    </div>
                    <div></div>
                </div>

                <div className={styles.Option}>
                    <div className={styles.optionaLabel}>افزودن لایه تیره تر</div>
                    <div className={styles.optionGear}>
                        <SwitchButton 
                            id="dark" 
                            status = {state.darkLayer}
                            onChangeValue = {() => setState({...state, darkLayer: !state.darkLayer})}
                        />
                    </div>
                </div>

                <div className={styles.Option}>
                    <div className={styles.optionaLabel}>افزودن لایه روشن تر</div>
                    <div className={styles.optionGear}>
                        <SwitchButton 
                            id="light"
                            status = {state.lightLayer}
                            onChangeValue = { () => setState({...state, lightLayer: !state.lightLayer}) }
                        />
                    </div>
                </div>

                <div className={styles.Option}>
                    <div className={styles.optionaLabel}>متد رنگ آمیزی</div>
                    <div className={styles.optionGear}>
                        <ToggleButton 
                            items={[{name:"۱"}, {name:"۲"}, {name:"۳"}]}
                            selected={state.currentColorIndex}
                            onSelected = {(index) => setState({...state, currentColorIndex: index})}
                        />
                    </div>
                </div>
            </div>
        );
    }
    
    return (
        <div className={styles.editContainer}>
            <div className={styles.topBar}>
                <FeatherIcon name="upload" ></FeatherIcon>
                <FeatherIcon name="arrow-right" onClick={()=>{
                    history.goBack();
                }}></FeatherIcon>
                <div class={styles.clearFix}></div>
            </div>
            <div className={styles.contentHolder}>
                
                <div className={styles.image}>
                    <div class={cn({
                        [styles.imageLoading]: true,
                        [styles.displayNone]: state.inferenceStatus === "Done",
                    })}>
                        <img src="./Circle_loader.svg"/>
                    </div>
                    <img 
                        src={window.localStorage.getItem("image")} 
                        alt="your uploaded photo_ to edit" 
                        id="yourPhoto"
                    />
                </div>
                
                <div className={styles.optionHolder}>
                    <div className={styles.modeSetting}>
                        <ToggleButton  
                            items={[{icon:"sliders", name:"حرفه ای"},
                            {icon: "droplet", name:"ساده"}]}
                            selected={state.currentStyleIndex}
                            onSelected = {(mode) => setState({...state, currentStyleIndex: mode})}
                        />
                    </div>
                    {options}
                {applyButton}
                </div>

                
            </div>
        </div>
    );

}