import styles from "./featherIcon.module.scss";
import cn from "classnames";

export default function FeatherIcon({name, className, onClick}){
    return (
        <svg className={cn({
            [styles.feather]: true,
            [className]: !!className,
        })} onClick={onClick}>
             <use xlinkHref={process.env.PUBLIC_URL + `/feather-sprite.svg#${name}`}/>
        </svg>
    )
}