import styles from './App.module.css';
import BigButton from '../../bigButton/bigButton'
import Gallery from '../../gallery/gallery'
import useWindowDimensions from '../../util/windowHook';
import { useHistory } from 'react-router';

export default function App() {
  const { width } = useWindowDimensions();
  const history = useHistory();
  return (
    <div className={styles.App}>
      <h1 className={styles.intro}>مو هاتو رنگ کن</h1>
      <p className={styles.intro}>
          با موهاتو رنگ کن میتونی بدون اینکه نیاز به رنگ کردن واقعی مو داشته باشی میتونی توی چند ثانیه ببینی که چه رنگ مویی بیشتر به چهره ات میاد و کلی وقت و هزینه ات صرفه جویی میشه
      </p>
      <h3 className={styles.title}>از عکس های قبلی انتخاب کن</h3>

      <Gallery className={styles.recently}
        rowNum={width < 800 ? 2 : 1}
        colNum={width < 800 ? 2 : 4} 
        images={["images/3.jpg", "images/4.jpg", "images/8.jpg"
        , "images/3.jpg"
      ]} onClick={(p)=>{
            window.localStorage.setItem("image", p);
            window.localStorage.setItem("mask", p.replace(".jpg", ".png"));
            history.push("/edit");
        }}/>

      <h3 className={styles.title}>یا یه عکس جدید امتحان کن</h3>
      <div className={styles.actionsHolder}>
        <div className={styles.bigButtons}>
          <BigButton title="از دوربین" icon="camera"/>
          <BigButton title="از گالری" icon="image"/>
        </div>
      </div>
    </div>
  );
}
