import React from 'react'
import styles from './galleryRow.module.css';
import cn from "classnames";
import FeatherIcon from '../feather/featherIcon'

export default function GalleryRow({images, selected, onClick}){
    return (
        <div className={styles.galleryRow}>
                {images.map((i, index) => {
                        return(<div className={cn({
                            [styles.holder]: true,
                            [styles.selected]: index === selected
                        })}>
                            <div className={styles.cover}>
                                <FeatherIcon name="check" />
                            </div>
                            <img 
                                className={styles.image} 
                                src={i} 
                                alt='recent-img'
                                onClick={ () => onClick && onClick(i)}
                            />
                        </div>)
                })}
        </div>
    )
}
