import styles from './toggleButton.module.css';
import FeatherIcon from '../../feather/featherIcon'
import cn from "classnames";

export default function ToggleButton({items, selected, onSelected}) {
    
    return (
        <div className={styles.buttons}>
            {items.map(({icon, name}, index) => (
                <button className={cn({
                    [styles.tiny]: !icon,
                    [styles.normal]: !!icon,
                    [styles.buttonSelected]: selected === index 
                })} onClick={()=>{
                    if(onSelected) onSelected(index);
                }}>
                    <span className={styles.name}>{name}</span>
                    {icon? (
                            <span className={styles.icon}>
                                <FeatherIcon name={icon}/>
                            </span>) 
                            : undefined}
                </button>
            ))}
        </div>
    );
}