import styles from './switchBttuon.module.css';

export default function SwitchButton({id, status, onChangeValue}) {
    return (
        <div>
            <input 
                class={styles.checkbox} 
                type="checkbox" 
                checked={status} 
                hidden="hidden"
                id={id}
                onChange={() => {
                    onChangeValue(status)
                }}
            />
            <label class={styles.switch} for={id}></label>
        </div>
    );
}